<template>
  <div class="bg-login">
    <div class="login-header container">
	</div>
      <div class="middle-boxlogin loginscreen bg-white container">
            <div class="login-title text-center">
                <h2 style="text-align:center;margin-top:5px;">用户登录</h2>
            </div>
            <el-form inline-message :model="loginForm" :rules="loginRules" ref="loginForm" class="login-Form">
                <el-form-item prop="name">
                    <el-input status-icon prefix-icon="fa fa-user" v-model="loginForm.name" placeholder="请输入用户名" ></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input prefix-icon="fa fa-lock" :type="passwordType" @keyup.enter.native="doLogin"
                        v-model="loginForm.password" placeholder="请输入密码" auto-complete="on">
                    </el-input>
                    <span class="show-pwd" :style="eyeStatus" @click="showPwd"><i class="icon iconfont icon-eye"></i></span>
                </el-form-item>
                <el-form-item label="">
                    <el-button type="primary" :loading="loading" @click.native.prevent="doLogin" style="width:400px;">登录</el-button>
                </el-form-item>
            </el-form>
            <div class="register_forget">
                <div class="register"><router-link to="/Register"> <a href="#">注册账户</a></router-link></div>
                <div class="forget_password"><router-link to="/Forget"><a href="#">忘记密码</a></router-link></div>
            </div>
      </div>
  </div>
</template>

<script>
//import store from '../store/index.js'
export default {
    name: 'Login',
    inject:['reload'],
    data() {
        return {
            loginForm:{
                name:'',
                password:''
            },
            loginRules:{
                name:[{required:true, message:'用户名不能为空',trigger:'blur'},{min:5, max:20, message:'长度在 5 到 20 个字符', trigger: 'blur'}],
                password:[{required:true,message:'密码不能为空',trigger:'blur'},{min:6,message:'密码长度必须6位以上',trigger:'blur'}]
            },
            loading:false,
            passwordType:'password',
            eyeStatus:'',
            token: ''
        }
    },
    watch: {
        token: {
            handler:function(token) {
                localStorage.save(token)
            },
            deep:true
        }
    },
    methods: {
        showPwd: function() {
            if (this.passwordType === 'password') {
                this.passwordType = ''
                this.eyeStatus = 'color:#1296db'
            }else {
                this.passwordType = 'password'
                this.eyeStatus = ''
            }
        },
        doLogin: function() {
            this.$refs.loginForm.validate(valid => {
                if (valid){
                    if (this.loginForm.name === null || this.loginForm.name === '') {
                        this.$message({
                            type:'error',
                            message: '用户名不能为空'
                        })
                        return;
                    }

                    if (this.loginForm.password === null || this.loginForm.password === '') {
                        this.$message({
                            type: 'error',
                            message: '密码不能为空'
                        })
                        return;
                    }
                    //this.loading = true
                    this.$http.post('/login',this.loginForm).then((res) => {
                        console.log(res.data)
                        if(res.data.code === 200) {
                                console.log("success")
                                this.$notify({
                                    type: 'success',
                                    message: res.data.message,
                                })
                                /*
                                this.$store.commit('set_token',res.data.token)
                                if (store.state.token) {
                                    this.$router.push('/Main')
                                    console.log(store.state.token)
                                }else {
                                    this.$router.push('/Login')
                                } */
                            
                                localStorage.setItem("token",res.data.token)
                                localStorage.setItem("username",this.loginForm.name)
                                //this.reload()
                                this.$router.push('/Home')
                            }else if (res.data.code === 1000) {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                return;
                            }
                    }).catch((err)=>{
                            this.$message({
                                type: 'error',
                                message: '网络错误，请重试' 
                            })
                    })
                } else{
                    return false
                }
            })
        }
    }
}
</script>

<style>
body{
    background: url("~@/assets/bk1.jpg") no-repeat center fixed;
    background-size: cover;
}
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.el-form-item__error--inline {
    display: flex;
}

.login-Form{
    margin-top: -30px;
}
.bg-white{
    background-color: #ffffff;
    opacity: 0.8;
}
.middle-boxlogin{
    max-width: 400px;
    z-index: 100;
    margin: 0 auto;
    padding-bottom: 10px;
    height: 360px;
}
.el-form {
  padding-top: 0;
}
.loginscreen .middle-box{
    width: 300px;
}
.login-title{
	height:85px;
	padding:30px;
	font-weight:bold;
}
.login-header{
	height:150px;
}
.login-logo{
	margin-top:40px;
}
.login-logo-zh,
.login-logo-en{
	font-weight:bold;
	color:#3b3127;
}
.login-logo-zh{
	font-size:45px;
	font-family:"宋体";
}
.login-logo-en{
	font-size:21px;
	font-family:"Helvetica";
}
.el-row {
    background: white;
}
/*
.middle-box{
    .el-input {
        display: inline-block;
    }
    .el-button {
        display: inline-block;
        width:100%;
    }
    .el-input__inner {
        text-align: center;
    }
}*/
.middle-box {
    height: 370px;
}
.show-pwd {
    position: absolute;
    right: 10px;
    top: 2px;
    font-size: 16px;
    color: #889aa4;
    cursor: pointer;
    user-select: none;
}
.register {
    float: left;
    font-size: 14px;
}
.forget_password {
    float: right;
    font-size: 14px;
}
a {
    text-decoration: none;
}
a:link{color:blue;}
a:visited{color:#409EFF;}
a:hover{color: red;font-size: 17px;}
a:active{color: lightskyblue;}
.el-input--prefix .el-input__inner{
    padding-left: 30px;
}
</style>


