<template>
  <div class="bg-login" style="margin-top:30px;">
    <div class="loginscreen bg-white container">
      <!-- middle-boxin -->
      <el-form
        inline-message
        :model="updateForm"
        :rules="registerRules"
        ref="updateForm"
        class="demo-ruleForm"
        label-width="30%"
      >
        <el-form-item label="工  号:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="updateForm.number"
            readonly
            placeholder="请输入工号"
            @change="validateNumber"
          ></el-input>
        </el-form-item>

        <el-form-item label="姓  名:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input class="input-editinfo" v-model="updateForm.name" placeholder="请输入姓名"  status-icon:true></el-input>
        </el-form-item>

        <el-form-item label="性  别:" required >
          <!-- style="padding-left:15%;" -->
          <el-select class="input-editinfo" v-model="updateForm.sex" placeholder="请输入性别" >
            <el-option label="男" value="male"></el-option>
            <el-option label="女" value="female"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="updateForm.email"
            placeholder="请输入邮箱"
            @change="validateEmail(updateForm.email)"
          ></el-input>
        </el-form-item>

        <el-form-item label>
          <el-button type="primary" style="width:150px;" @click="Save()">编辑</el-button>
          <!-- style="margin-left:1100px;" -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "EditInfo",
  inject: ["reload"],
  data() {
    return {
      updateForm: {
        number: "",
        name: "",
        password: "",
        sex: "",
        email: ""
      },
      readonly:true,
      ackpassword: "",
      registerRules: {
        number: [
          { required: true, message: "工号不能为空", trigger: "blur" },
          { min: 10, max: 20, message: "长度在10到20个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
          { min: 5, max: 20, message: "长度在 5 到 20 个字符", trigger: "blur" }
        ],
        sex: [{ required: true, message: "性别不能为空", trigger: "blur" }],
        email: [
          { required: true, message: "邮箱不能为空", trigger: "blur" },
          {
            min: 10,
            max: 20,
            message: "邮箱长度在10到20个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    var token = localStorage.getItem("token");
    var teacherName = localStorage.getItem("username");
    console.log("name === " + teacherName);
    console.log("token == " + token);
    if (token) {
      this.$http({
        methods: "get",
        url: "/getInfo?teacherName=" + teacherName,
        headers: {
          token: token
        }
      }).then(response => {
          if (response.data.code == 200) {
            console.log("success");
            this.updateForm = response.data.teacher;
            this.updateForm.password = "";
          } else {
            this.$router.push("/Login");
          }
        }).catch(error => {
          this.$message({
            type: "error",
            message: "请登录后再试！"
          });
        });
    } else {
      this.$router.push("/Login");
    }
  },
  mounted() {
    var obj = document.getElementsByClassName("input-editinfo");
  },
  watch() {
     var token = localStorage.getItem("token");
     if (token==''||token == null) {
       this.$router.push('/Login')
     }
  },
  methods: {
    validateNumber: function(value) {
      if (value == "") {
        this.$message({
          type: "error",
          message: "工号不能为空"
        });
      } else {
        if (value != "") {
          var reg = 10 && /^(\d{9,13})$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的工号"
            });
          }
        }
      }
    },
    validateEmail: function(value) {
      if (value === "") {
        this.$message({
          type: "error",
          message: "请正确填写邮箱"
        });
      } else {
        if (value !== "") {
          var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "请输入有效的邮箱"
            });
          }
        }
      }
    },
    Save: function() {
      this.$refs.updateForm.validate(valid => {
        if (valid) {
          var token = localStorage.getItem("token");
          //alert("token = " + token);
          this.readonly = false
          if (token) {
            this.$http.post("/editInfo",this.updateForm,{headers:{"token":token}})
              .then(res => {
                console.log(res.data);
                if (res.data.code === 200) {
                  //console.log("success");
                  this.$message({
                    type: "success",
                    message: res.data.message
                  });
                  this.$router.push("/EditInfo");
                } else if (res.data.code === 1024) {
                  this.$message({
                    type: "error",
                    message: "更新失败"
                  });
                  return;
                } else {
                  this.$message({
                    type: "error",
                    message: "更新失败"
                  });
                  return;
                }
              })
              .catch(err => {
                this.$message({
                  type: "error",
                  message: "网络错误，请重试"
                });
              });
          }
        } else {
         this.$message({
                  type: "error",
                  message: "网络错误，请重试"
                });
        }
      });
    }
  }
};
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
/* .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
} */
.el-form-item__error--inline {
  display: flex;
}

.demo-ruleForm{
  margin-top: -30px;
}
.demo-ruleForm {
  padding-top: 65px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-boxin {
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  /* min-width: 600px; */
  height: 400px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/* .middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
} */

.show-pwd {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 16px;
  color: #889aa4;
  cursor: pointer;
  user-select: none;
}
.input-editinfo {
  width: 500px;
}
.el-row {
  background: white;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
.el-input--prefix .el-input__inner {
  padding-left: 10px;
}
a {
  text-decoration: none;
}
a:link {
  color: blue;
}
a:visited {
  color: #409eff;
}
a:hover {
  color: red;
  font-size: 17px;
}
a:active {
  color: lightskyblue;
}
</style>


