<template>
  <div class="bg-login">
    <div class="appeal-header container">
	</div>
      <div class="middle-boxlappeal appealscreen bg-white container">
            <div class="appeal-title text-center">
                <h2 style="text-align:center;margin-top:5px;">找回密码</h2>
            </div>
            <el-form inline-message :model="appealForm" :rules="appealRules" ref="appealForm">
                <el-form-item prop="email">
                    <el-input status-icon prefix-icon="fa fa-user" v-model="appealForm.email" placeholder="请输入邮箱" @blur="validateEmail(appealForm.email)" ></el-input>
                </el-form-item>
                <el-form-item prop="checkCode">
                    <el-input prefix-icon="fa fa-lock" @keyup.enter.native="submit"
                        v-model="appealForm.checkCode" placeholder="请输入验证码" auto-complete="on">
                    </el-input>
                    <div class="divIdentifyingCode" @click="getIdentifyingCode(true)">
                        <img id="imgIdentifyingCode" style="height:40px; width: 100px; cursor: pointer;" alt="点击更换"
                            title="点击更换" />
                    </div>
                </el-form-item>
                <el-form-item label="">
                    <el-button type="primary" :loading="loading" @click.native.prevent="submit" style="width:400px;">提交</el-button>
                </el-form-item>
            </el-form>
            <div class="last_step">
                <div class="laststep"><router-link to="/Forget"> <a href="#">上一步</a></router-link></div>
            </div>
      </div>
  </div>
</template>

<script>
//import store from '../store/index.js'
export default {
    name: 'appealPass',
    inject:['reload'],
    data() {
        return {
            appealForm:{
                email:'',
                checkCode:''
            },
            appealRules:{
                email:[{required:true, message:'邮箱不能不能为空',trigger:'blur'},{min:5, max:20, message:'长度在 5 到 20 个字符', trigger: 'blur'}],
                checkCode:[{required:true,message:'验证不能为空',trigger:'blur'},{min:4,message:'验证码长度必须为4位',trigger:'blur'}]
            },
            loading:false,
            token: ''
        }
    },
    created() {
        
    },
    mounted() {
        // this.url = "http://localhost:8088/api/createImageCode?" + Math.random();
        // let objs = document.getElementById("imgIdentifyingCode");
        // objs.src = this.url;
        this.getIdentifyingCode(true);
    },
    watch: {
        token: {
            handler:function(token) {
                localStorage.save(token)
            },
            deep:true
        }
    },
    methods: {
        validateEmail: function(value){
            if (value === '') {
            this.$message({
                type:'error',
                message: '请正确填写邮箱'
            })
            } else {
            if (value !== '') { 
                var reg=/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if(!reg.test(value)){
                this.$message({
                type:'error',
                message: '请输入有效的邮箱'
                })
                }
            }
            }
        },
        submit: function() {
            this.$refs.appealForm.validate(valid => {
                if (valid){
                    if (this.appealForm.name === null || this.appealForm.name === '') {
                        this.$message({
                            type:'error',
                            message: '用户名不能为空'
                        })
                        return;
                    }

                    if (this.appealForm.checkCode === null || this.appealForm.checkCode === '') {
                        this.$message({
                            type: 'error',
                            message: '验证码不能为空'
                        })
                        return;
                    }
                    //this.loading = true
                    this.$http.get('/appeal?email='+this.appealForm.email + '&checkCode='+this.appealForm.checkCode).then((res) => {
                        console.log(res.data)
                        if(res.data.code === 200) {
                                console.log("success")
                                this.$message({
                                    type: 'success',
                                    message: res.data.message,
                                })
                                this.$router.push('/Login')
                            }else if(res.data.code == 1023){
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }else if (res.data.code == 1024) {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }
                    }).catch((err)=>{
                            this.$message({
                                type: 'error',
                                message: '网络错误，请重试' 
                            })
                            this.getIdentifyingCode(true);
                    })
                    
                }
            })
        },
        /**
         * 窗口代码
         * @param {Boolean} bRefresh 是否刷新
         */
        getIdentifyingCode: function (bRefresh) {
            let identifyCodeSrc =  "http://localhost:8088/api/createImageCode";
            if (bRefresh) {
                identifyCodeSrc = "http://localhost:8088/api/createImageCode?" + Math.random();
            }
            let objs = document.getElementById("imgIdentifyingCode");
            objs.src = identifyCodeSrc;
            }
        }
}
</script>

<style>
body{
    background: url("~@/assets/bk1.jpg") no-repeat center fixed;
    background-size: cover;
}
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.el-form-item__error--inline {
    display: flex;
}
.el-form {
    margin-top: -30px;
}
.bg-white{
    background-color: #ffffff;
    opacity: 0.8;
}
.middle-boxlappeal{
    max-width: 400px;
    z-index: 100;
    margin: 0 auto;
    padding-bottom: 10px;
}
.el-form {
  padding-top: 0;
}
.appealscreen .middle-box{
    width: 300px;
}
.appeal-title{
	height:85px;
	padding:30px;
	font-weight:bold;
}
.appeal-header{
	height:150px;
}
.appeal-logo{
	margin-top:40px;
}
.appeal-logo-zh,
.appeal-logo-en{
	font-weight:bold;
	color:#3b3127;
}
.appeal-logo-zh{
	font-size:45px;
	font-family:"宋体";
}
.appeal-logo-en{
	font-size:21px;
	font-family:"Helvetica";
}
.middle-box {
    height: 370px;
}
.divIdentifyingCode {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    width: 102px; /*设置图片显示的宽*/
    height: 40px; /*图片显示的高*/
    background: #e2e2e2;
    margin: 0;
  }
a {
    text-decoration: none;
}
a:link{color:blue;}
a:visited{color:#409EFF;}
a:hover{color: red;font-size: 17px;}
a:active{color: lightskyblue;}
</style>


