<template>
  <div class="bg-login">
    <div class="update-header container">
	</div>
      <div class="middle-boxlupdate updatescreen bg-white container">
            <div class="update-title text-center">
                <h2 style="text-align:center;margin-top:5px;">找回密码</h2>
            </div>
            <el-form inline-message :model="updateForm" :rules="updateRules" ref="updateForm">
                <el-form-item prop="name">
                    <el-input status-icon prefix-icon="fa fa-user" v-model="updateForm.name" placeholder="请输入用户名" ></el-input>
                </el-form-item>
               <el-form-item prop="password">
                    <el-input prefix-icon="fa fa-lock"  @keyup.enter.native="update"
                        v-model="updateForm.password" placeholder="请输入密码" auto-complete="on">
                    </el-input>
                    <span class="show-pwd" ><i class="icon iconfont icon-eye"></i></span>
                </el-form-item>
                <el-form-item prop="ack_password">
                    <el-input prefix-icon="fa fa-lock" @keyup.enter.native="update"
                        v-model="updateForm.ack_password" placeholder="请确认密码" auto-complete="on">
                    </el-input>
                    <span class="show-pwd"  ><i class="icon iconfont icon-eye"></i></span>
                </el-form-item>
                <el-form-item label="">
                    <el-button type="primary" :loading="loading" @click.native.prevent="update" style="width:400px;">更新</el-button>
                </el-form-item>
            </el-form>
      </div>
  </div>
</template>

<script>
//import store from '../store/index.js'
export default {
    name: 'updatePassword',
    inject:['reload'],
    data() {
        return {
            updateForm:{
                name:'',
                password:'',
                ack_password:''
            },
            updateRules:{
                name:[{required:true, message:'用户名不能为空',trigger:'blur'},{min:5, max:20, message:'长度在 5 到 20 个字符', trigger: 'blur'}],
                password:[{required:true,message:'密码不能为空',trigger:'blur'},{min:6,message:'密码长度必须6位以上',trigger:'blur'}],
                ack_password:[{required:true,message:'密码不能为空',trigger:'blur'},{min:6,message:'密码长度必须6位以上',trigger:'blur'}]
            },
            loading:false,
        }
    },
    methods: {
        update: function() {
            this.$refs.updateForm.validate(valid => {
                if (valid){
                    if (this.updateForm.password != this.updateForm.ack_password) {
                        this.$message({
                            type:'error',
                            message: '前后密码不一致!'
                        })
                        return;
                    }
                    //this.loading = true
                    //alert(this.updateForm.name)
                    this.$http.post('/updatePassword?name='+this.updateForm.name+"&password="+this.updateForm.password).then((res) => {
                        console.log(res.data)
                        if(res.data.code === 200) {
                                console.log("success")
                                this.$message({
                                    type: 'success',
                                    message: res.data.message,
                                })
                                //this.$router.push('/Appeal')
                            }else if(res.data.code == 1023){
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                               
                            }else if (res.data.code == 400) {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                               
                            }else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                
                            }
                    }).catch((err)=>{
                            this.$message({
                                type: 'error',
                                message: '网络错误，请重试' 
                            })
                        
                    })
                    
                }
            })
        }
    }
}
</script>

<style>
body{
    background: url("~@/assets/bk1.jpg") no-repeat center fixed;
    background-size: cover;
}
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.el-form-item__error--inline {
    display: flex;
}
.el-form {
    margin-top: -30px;
}
.bg-white{
    background-color: #ffffff;
    opacity: 0.8;
}
.middle-boxlupdate{
    max-width: 400px;
    z-index: 100;
    margin: 0 auto;
    padding-bottom: 10px;
}
.el-form {
  padding-top: 0;
}
.updatescreen .middle-box{
    width: 300px;
}
.update-title{
	height:85px;
	padding:30px;
	font-weight:bold;
}
.update-header{
	height:150px;
}
.update-logo{
	margin-top:40px;
}
.update-logo-zh,
.update-logo-en{
	font-weight:bold;
	color:#3b3127;
}
.update-logo-zh{
	font-size:45px;
	font-family:"宋体";
}
.update-logo-en{
	font-size:21px;
	font-family:"Helvetica";
}
.middle-box {
    height: 370px;
}
.divIdentifyingCode {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    width: 102px; /*设置图片显示的宽*/
    height: 40px; /*图片显示的高*/
    background: #e2e2e2;
    margin: 0;
  }
a {
    text-decoration: none;
}
a:link{color:blue;}
a:visited{color:#409EFF;}
a:hover{color: red;font-size: 17px;}
a:active{color: lightskyblue;}
</style>


