<template>
  <div class="bg-login">
    <div class="login-header container"></div>
    <div class="middle-box loginscreen bg-white container">
      <div class="login-title text-center">
        <h2 style="text-align:center;margin-top:5px;">用户注册</h2>
      </div>
      <el-form  inline-message :inline="true" :model="registerForm" :rules="registerRules" ref="registerForm" class="demo-form-inline">
        <div>
          <el-form-item label="工  号:" required>
            <el-input v-model="registerForm.number" placeholder="请输入工号" @blur="validateNumber(this)"></el-input>
          </el-form-item>
          <el-form-item label="姓  名:" style="float:right;" required>
            <el-input v-model="registerForm.name" placeholder="请输入姓名"></el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="密  码:" required prop="password">
            <el-input
              prefix-icon="fa fa-lock"
              
              @keyup.enter.native="doRegister"
              v-model="registerForm.password"
              type="password"
              placeholder="请输入密码"
              auto-complete="on"
            ></el-input>
            <span class="show-pwd">
              <i class="icon iconfont icon-eye"></i>
            </span>
          </el-form-item>
          <el-form-item label="确认密码:" style="float:right;" required>
            <el-input
              prefix-icon="fa fa-lock"
              type="password"
              @keyup.enter.native="doRegister"
              v-model="ackpassword"
              placeholder="请确认密码"
              auto-complete="on"
            ></el-input>
            <span class="show-pwd">
              <i class="icon iconfont icon-eye"></i>
            </span>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="性  别:" required>
            <el-select v-model="registerForm.sex" placeholder="请输入性别">
              <el-option label="男" value="male"></el-option>
              <el-option label="女" value="female"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="邮箱:" style="float:right;" required>
            <el-input v-model="registerForm.email" placeholder="请输入邮箱" @blur="validateEmail(registerForm.email)"></el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label>
            <el-button
              type="primary"
              @click.native.prevent="doRegister"
              style="width:400px; margin-left:100px;"
            >注册</el-button>
          </el-form-item>
        </div>
      </el-form>
      <div class="tologin">
        <div class="login" style="float:left;">
          <a href="/Login">已有账号，去登录</a>
        </div>
        <div class="reset" style="float:right;">
          <a href="#" @click="reset()">重置</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  inject:['reload'],
  data() {
    return {
      registerForm: {
        number: "",
        name: "",
        password: '',
        sex: '',
        email: ''
      },
      ackpassword: '',
      registerRules:{
                number:[{required:true,message:'工号不能为空',trigger:'blur'},{min:10,max:20,message:'长度在10到20个字符',trigger:'blur'}],
                name:[{required:true, message:'用户名不能为空',trigger:'blur'},{min:5, max:20, message:'长度在 5 到 20 个字符', trigger: 'blur'}],
                password:[{required:true,message:'密码不能为空',trigger:'blur'},{min:6,message:'密码长度必须6位以上',trigger:'blur'}],
                sex:[{required:true,message:'性别不能为空',trigger:'blur'}],
                email:[{required:true,message:'邮箱不能为空',trigger:'blur'},{min:10,max:20,message:'邮箱长度在10到20个字符',trigger:'blur'}]
            }
    };
  },
  methods: {
    reset: function() {
      this.registerForm.name = '';
      this.registerForm.password = '';
      this.registerForm.sex = '';
      this.registerForm.email = '';
      this.registerForm.number = '';
      this.ackpassword = '';
    },
    /*
    validatePassword: function(value) {
        if (value === '') {
          this.$message({
            type: 'error',
            message: '请正确填写密码'
          })
        }else {
          if (value !== '') {
            var reg = /^[0-9A-Za-z]{6,15}$/
            if (!reg.test(value)) {
              this.$message({
              type:'error',
              message: '密码长度小于6位!'
              })
            }
          }
        }
    },*/
    validateNumber: function(value) {
       if (value == '') {
         this.$message({
           type:'error',
           message:'工号不能为空'
         })
       }else {
         if (value != '') {
           var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
           if (!reg.test(value)) {
             this.$message({
               type:'error',
               message: '请输入有效的工号'
             })
           }
         }
         
       }
       
    },
    validateEmail: function(value){
        if (value === '') {
          this.$message({
            type:'error',
            message: '请正确填写邮箱'
          })
        } else {
          if (value !== '') { 
            var reg=/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
            if(!reg.test(value)){
              this.$message({
              type:'error',
              message: '请输入有效的邮箱'
            })
            }
          }
        }
    },
    doRegister: function() {
            this.$refs.registerForm.validate(valid => {
                if (valid){
                    if(this.registerForm.password != this.ackpassword) {
                        this.$message({
                            type:'error',
                            message:'前后密码不一致！'
                        })
                        return false;
                    }  
                    //this.loading = true
                    this.$http.post('/register',this.registerForm).then((res) => {
                        console.log(res.data)
                        if(res.data.code === 200) {
                                console.log("success")
                                this.$message({
                                    type: 'success',
                                    message: res.data.message,
                                })
                                this.reload()
                                this.$router.push('/Login')
                            }else if (res.data.code === 1001) {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                return;
                            }else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                return;
                            }
                    }).catch((err)=>{
                            this.$message({
                                type: 'error',
                                message: '网络错误，请重试' 
                            })
                    })
                } else{
                    return false
                }
            })
        }
    }
  }
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.el-form-item__error--inline {
  display: flex;
}

.demo-form-inline{
  margin-top: -30px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-box {
  max-width: 400px;
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  min-width: 600px;
  height: 440px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/*
.middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
}
*/
.show-pwd {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 16px;
  color: #889aa4;
  cursor: pointer;
  user-select: none;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
.el-input--prefix .el-input__inner {
  padding-left: 10px;

}
 .el-input__inner {
  width: 210px;
}
a {
  text-decoration: none;
}
a:link {
  color: blue;
}
a:visited {
  color: #409eff;
}
a:hover {
  color: red;
  font-size: 17px;
}
a:active {
  color: lightskyblue;
}
</style>


