<template>
  <div style="background-color: #EBEBEB;min-height:900px">
    <div style="width:100%;background-color:#143f6d; overflow: hidden">
      <span class="demonstration" style="float:left;padding-top:10px;color:white;margin-left:50%;">
        <h2 style="color:white">中学生考勤系统</h2>
      </span>
      <span class="demonstration" style="float:right;padding-top:30px;margin-right:1%">
        <span class="message" style="color:white;padding-right:20px;">
          <router-link to="#">
            <a href="#">
              <i class="el-icon-message"></i>
              <span slot="title">消息中心</span>
            </a>
          </router-link>
        </span>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link" style="color:white">
            {{username}}
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown" @select="handleClick">
            <el-dropdown-item @click.native="editInfo">个人信息</el-dropdown-item>
            <el-dropdown-item @click.native="loginout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </span>
    </div>

    <div>
      <el-row :span="24">
        <el-col :xs="4" :sm="4" :md="4" :lg="4">
          <div>
            <el-menu
              default-active="1"
              class="el-menu-vertical-demo"
              style="min-height:720px"
              background-color="#0d5477"
              text-color="white"
              active-text-color="#ffd04b"
              @select="handleSelect"
            >
              <el-menu-item index="1">
                <i class="el-icon-search"></i>
                <span slot="title">查询签到信息</span>
              </el-menu-item>
              <el-menu-item index="2">
                <i class="el-icon-view"></i>
                <span slot="title">数据可视化</span>
              </el-menu-item>
              <el-menu-item index="3">
                <i class="el-icon-upload"></i>
                <span slot="title">上传签到文件</span>
              </el-menu-item>
              <el-menu-item index="4">
                <i class="el-icon-setting"></i>
                <span slot="title">修改密码</span>
              </el-menu-item>
              <el-menu-item index="5">
                <i class="el-icon-edit-outline"></i>
                <span slot="title">权限管理</span>
              </el-menu-item>
            </el-menu>
          </div>
        </el-col>
        <el-col :xs="20" :sm="20" :md="20" :lg="20">
          <!-- <div>
            <div
              style="border: 1px solid #A6A6A6; border-radius:6px; padding:5px; margin:2px; background-color: white"
            >
              <el-breadcrumb separator="/"></el-breadcrumb>
            </div>
          </div>-->

          <div style="margin-top:5px">
            <router-view></router-view>
          </div>
        </el-col>
      </el-row>
    </div>
    <div style="width:100%;background-color:#143f6d; overflow: hidden;height:100px;">
      <span class="demonstration" style="float:left;padding-top:15px;color:white;margin-left:50%;">
        <div class="copyright" style="color:white;margin-top:22px;">Copyright&copy;中国地质大学(北京)</div>
      </span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "Home",
  data() {
    return {
      searchCriteria: "",
      breadcrumbItems: ["查询签到信息"],
      username: ""
    };
  },
  created() {
    var token = localStorage.getItem("token");
    if (token) {
      this.$router.push("/Courses");
      this.username = localStorage.getItem("username");
    } else {
      this.$router.push("/Login");
    }
  },
  mounted() {},
  methods: {
    loginout() {
      this.$confirm("确定注销吗?")
        .then(_ => {
          var token = localStorage.getItem("token");
          if (token) {
            this.$http({
              methods: "get",
              url: "/loginout",
              headers: { token: token }
            })
              .then(response => {
                if (response.data.code == 200) {
                  localStorage.removeItem("token");
                  localStorage.removeItem("username");
                  this.$message({
                    type: "success",
                    message: "注销成功！"
                  });
                  this.$router.push("/Login");
                } else {
                  this.$message({
                    type: "error",
                    message: "网络错误!"
                  });
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: "网络错误!"
                });
              });
          } else {
            this.$message({
              type: "error",
              message: "网络错误!"
            });
            this.$router.push("/Login");
          }
        })
        .catch(_ => {});
    },
    editInfo() {
      var token = localStorage.getItem("token");
      if (token) {
        this.$router.push("/EditInfo");
      } else {
        this.$router.push("/Login");
      }
    },
    handleClick(key, keyPath) {
      switch (key) {
        case "1":
          console.log("info");
          break;
        case "2":
          this.loginout();
          break;
      }
    },

    handleSelect(key, keyPath) {
      switch (key) {
        case "1":
          this.$router.push("/Courses");
          this.breadcrumbItems = ["查询签到信息"];
          break;
        case "2":
          this.$router.push("/DataView");
          this.breadcrumbItems = ["数据可视化"];
          break;
        case "3":
          this.$router.push("/UploadFile");
          this.breadcrumbItems = ["上传签到文件"];
          break;
        case "4":
          this.$router.push("/ChangePass");
          this.breadcrumbItems = ["修改密码"];
          break;
      }
    }
  }
};
</script>
<style scoped>
.el-row {
  background: white;
}
.el-input--prefix .el-input__inner {
  padding-left: 30px;
}
.message a:link{
  color: white;
}
a:active {
  color: white;
}
a {
  text-decoration: none;
}
</style>





