                    <el-input v-model="queryForm.studentName" placeholder="请输入学生姓名"></el-input>
                  </el-form-item>
                  <el-form-item label="时间" required>
                    <el-col :span="11">
                      <el-form-item prop="date1">
                        <el-date-picker
                          type="date"
                          placeholder="选择起始日期"
                          v-model="queryForm.startDate"
                          @change="getDate"
                          format="yyyy-MM-dd"
