<script>
import { Students } from "../utils/students";
export default {
  name: "Main",
  created() {
    this.Students = Students;
  },
  data() {
    return {
      queryForm: {
        className: "",
        studentName: "",
        startDate: "",
        endDate: ""
      },
      qurryFormRules: {},
      Students: [
        {
          date: "2019-12-21",
          className: "高一(1)班",
          totalNumber: 21,
          actualNumber: 20,
          teacher: "含笑半步癫"
        },
        {
          date: "2019-12-21",
          className: "高一(1)班",
          totalNumber: 21,
          actualNumber: 20,
          teacher: "含笑半步癫"
        },
        {
          date: "2019-12-21",
          className: "高一(1)班",
          totalNumber: 21,
          actualNumber: 20,
          teacher: "含笑半步癫"
        },
        {
          date: "2019-12-21",
          className: "高一(1)班",
          totalNumber: 21,
          actualNumber: 20,
          teacher: "含笑半步癫"
        }
      ]
    };
  },
  created() {},
  methods: {
    test: function() {
       
    },
    handleClick(row) {
      console.log(row.date + "-" + row.className);
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return "warning-row";
      } else if (rowIndex === 3) {
        return "success-row";
      }
      return "";
    },
    getDate(date) {
      //const temp = new Date(date);
      if (date != "" || date != undefined) {
        var year = date.getFullYear();
        var month =
          date.getMonth() + 1 >= 10
            ? date.getMonth() + 1
            : "0" + (date.getMonth() + 1);
        var day = date.getDay() >= 10 ? date.getDay() : "0" + date.getDay();
        //this.queryForm.startDate = year + '-' + month + '-' + day;
        //console.log(this.queryForm.startDate)
        var newDate = year + "-" + month + "-" + day;
        return newDate;
      }
    },
    onSubmit: function() {
      this.queryForm.startDate = this.getDate(this.queryForm.startDate);
      console.log(this.queryForm.startDate);
    }
  }
};
</script>

