<template>
  <div class="bg-login" style="margin-top:30px;">
    <div class="loginscreen bg-white container">
      <el-form
        inline-message
        :model="updateForm"
        :rules="updateRules"
        ref="updateForm"
        class="demo-ruleForm"
        label-width="30%"
      >
        <!-- middle-boxin -->
        <el-form-item label="原密码:" required prop="oldpassword">
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            prefix-icon="fa fa-lock"
            v-model="updateForm.oldpassword"
            type="password"
            placeholder="请输入原密码"
            auto-complete="on"
          ></el-input>
          <span class="show-pwd">
            <i class="icon iconfont icon-eye"></i>
          </span>
        </el-form-item>
        <el-form-item label="新密码:" required prop="password">
          <!-- style="float:right;padding-right:15%;" -->
          <el-input
            class="input-editinfo"
            prefix-icon="fa fa-lock"
            type="password"
            v-model="updateForm.password"
            placeholder="请输入新密码"
            @onchange="validatePassword"
            auto-complete="on"
          ></el-input>
          <span class="show-pwd">
            <i class="icon iconfont icon-eye"></i>
          </span>
        </el-form-item>
        <el-form-item label="确认密码:" required prop="ackpassword">
          <!-- style="float:right;padding-right:15%;" -->
          <el-input
            class="input-editinfo"
            prefix-icon="fa fa-lock"
            type="password"
            v-model="updateForm.ackpassword"
            placeholder="请确认新密码"
            @onchange="validatePassword"
            auto-complete="on"
          ></el-input>
          <span class="show-pwd">
            <i class="icon iconfont icon-eye"></i>
          </span>
        </el-form-item>
        <el-form-item label>
          <el-button type="primary" style="width:150px;" @click.native.prevent="Save">更改</el-button>
          <!-- style="margin-left:1100px;" -->
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChangePass",
  inject: ["reload"],
  data() {
    return {
      updateForm: {
        oldpassword: "",
        password: "",
        ackpassword: ""
      },
      updateRules: {
        oldpassword: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码长度必须6位以上", trigger: "blur" }
        ],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码长度必须6位以上", trigger: "blur" }
        ],
        ackpassword: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码长度必须6位以上", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    
  },
  methods: {
    validatePassword: function(value) {
      if (value === "") {
        this.$message({
          type: "error",
          message: "请正确填写密码"
        });
      } else {
        if (value !== "") {
          var reg = /^[0-9A-Za-z]{6,15}$/;
          if (!reg.test(value)) {
            this.$message({
              type: "error",
              message: "密码长度小于6位!"
            });
          }
        }
      }
    },
    Save: function() {
      this.$refs.updateForm.validate(valid => {
        if (valid) {
          //this.loading = true
          var username = localStorage.getItem("username");
          //alert(username)
          if (this.updateForm.oldpassword == this.updateForm.password) {
            this.$message({
              type: "error",
              message: "新密码与原密码一致！"
            });
            return false;
          }

          if (this.updateForm.password != this.updateForm.ackpassword) {
            this.$message({
              type: "error",
              message: "前后密码不一致!"
            });
            return false;
          }
          var token = localStorage.getItem("token");
          if (true) {
            var user = {};
            user.name = username;
            user.password = this.updateForm.oldpassword;
            this.$http.post("/findByTeacher", user).then((res) => {
              if (res.data.code == 200) {
                //alert("success")
                var data = {};
                data.name = username;
                data.password = this.updateForm.password;
                this.$http.post("/updatePassword",data).then((res)=>{
                    if (res.data.code == 200) {
                      this.$message({
                        type:'success',
                        message:res.data.message
                      })
                      this.$router.push("/Courses");
                    }else {
                       this.$message({
                        type:'success',
                        message:res.data.message
                      })
                    }
                }).catch((err)=>{
                  this.$message({
                    type:'error',
                    message:'操作失败'
                  })
                })
              } else {
                this.$message({
                  type: "error",
                  message: res.data.message
                });
              }
            });
          }
        }
      });
    }
  }
};
</script>

<style>
body {
  background: url("~@/assets/bk1.jpg") no-repeat center fixed;
  background-size: cover;
}
/* .container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
} */
.el-form-item__error--inline {
  display: flex;
}

.demo-ruleForm {
  padding-top: 65px;
}
.bg-white {
  background-color: #ffffff;
  opacity: 0.8;
}
.middle-boxin {
  z-index: 100;
  margin: 0 auto;
  padding-bottom: 10px;
  /* min-width: 600px; */
  height: 400px;
}
.loginscreen .middle-box {
  width: 300px;
}
.login-title {
  height: 85px;
  padding: 30px;
  font-weight: bold;
}
.login-header {
  height: 150px;
}
.login-logo {
  margin-top: 40px;
}
.login-logo-zh,
.login-logo-en {
  font-weight: bold;
  color: #3b3127;
}
.login-logo-zh {
  font-size: 45px;
  font-family: "宋体";
}
.login-logo-en {
  font-size: 21px;
  font-family: "Helvetica";
}
/* .middle-box {
  .el-input {
    display: inline-block;
  }
  .el-button {
    display: inline-block;
    width: 100%;
  }
  .el-input__inner {
    text-align: center;
  }
} */

.show-pwd {
  position: absolute;
  right: 10px;
  top: 2px;
  font-size: 16px;
  color: #889aa4;
  cursor: pointer;
  user-select: none;
}
.input-editinfo {
  width: 500px;
}
.el-row {
  background: white;
}
.register {
  float: left;
}
.forget_password {
  float: right;
}
/*
.el-input--prefix .el-input__inner {
  padding-left: 10px;
}*/
a {
  text-decoration: none;
}
a:link {
  color: blue;
}
a:visited {
  color: #409eff;
}
a:hover {
  color: red;
  font-size: 17px;
}
a:active {
  color: lightskyblue;
}
</style>


