<template>
  <div class="main_content">
    <div id="bedroom" style="float:left;"></div>
    <div id="test"></div>
  </div>
</template>
<script>
import echarts from "echarts";
export default {
  data() {
    return {
      chart: null,
      opinion: ["旷课", "早退", "正常", "迟到"],
      opinionData: [
        { value: 3, name: "旷课" },
        { value: 1, name: "早退" },
        { value: 18, name: "正常" },
        { value: 5, name: "迟到" }
      ]
    };
  },
  created() {},
  methods: {
    drawLine(id) {
      this.charts = echarts.init(document.getElementById("test"));
      this.charts.setOption({
        title:{
          text:'签到时间走势',
          left:"center",
          top: 10,
          textStyle: {
            fontSize: 18,
            fontFamily: "Helvetica",
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item",
          formatte: "{b}: {c} ({d}%)"
        },
        toolbox: {
          feature: {
            saveAsImage: {},
            dataView: {}
          },
          right: 15,
          top: 10
        },
         xAxis: {
          type: "category",
          data: ["07:00", "07:15", "07:30", "07:45", "08:00", "08:15", "08:30"]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [5, 6, 15, 12, 10, 1, 1],
            type: "line"
          }
        ]
      })
    },
    drawPie(id) {
      this.chart = echarts.init(document.getElementById("bedroom"));
      this.chart.setOption({
        title: {
          text: "考勤情况",
          left: "center",
          top: 10,
          textStyle: {
            fontSize: 18,
            fontFamily: "Helvetica",
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item",
          formatte: "{b}: {c} ({d}%)"
        },
        toolbox: {
          feature: {
            saveAsImage: {},
            dataView: {}
          },
          right: 15,
          top: 10
        },
        legend: {
          orient: "vertical",
          left: 5,
          top: 10,
          data: this.opinion
        },
        series: [
          {
            name: "考勤情况",
            type: "pie",
            radius: ["50%", "70%"],
            center: ["50%", "60%"],
            avoidLabelOverlap: false,
            label: {
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "24",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this.opinionData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffset: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      });
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.drawPie("bedroom");
      this.drawLine("test")
    });
  }
};
</script>
<style scoped>
#bedroom {
  position: relative;
  left: 46%;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  margin-left: -555px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}

#test {
  position: absolute;
  left: 50%;
  margin-left: 130px;
  margin-bottom: 70px;
  width: 600px;
  height: 400px;
  margin-top: 80px;
  border: solid #d01257 1px;
  box-shadow: 0 0 8px #fb90b7;
  border-radius: 10px;
}
</style>


