<template>
  <div class="bg-login" style="margin-top:30px;">
    <div class="loginscreen bg-white container">
      <!-- middle-boxin -->
      <el-form
        inline-message
        :model="updateForm"
        :rules="registerRules"
        ref="updateForm"
        class="demo-ruleForm"
        label-width="30%"
      >
        <el-form-item label="工  号:" required>
          <!-- style="padding-left:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="updateForm.number"
            placeholder="请输入工号"
          readonly:true></el-input>
        </el-form-item>

        <el-form-item label="姓  名:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input class="input-editinfo" v-model="updateForm.name" placeholder="请输入姓名" readonly:true></el-input>
        </el-form-item>

        <el-form-item label="性  别:" required >
          <!-- style="padding-left:15%;" -->
         <el-input class="input-editinfo" v-model="updateForm.sex" placeholder="请输入邮箱" readonly:true></el-input>
         </el-form-item>
        <el-form-item label="邮箱:" required>
          <!-- style="float:right;padding-right:15%;" -->
          <el-input
            class="input-editinfo"
            v-model="updateForm.email"
            placeholder="请输入邮箱"
           readonly:true></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Info",
  inject: ["reload"],
  data() {
    return {
      updateForm: {
        number: "",
        name: "",
        password: "",
        sex: "",
        email: ""
      },
      token:localStorage.getItem("token"),
      registerRules: {
      }
    };
  },
  watch: {
      token(newToken,oldToken) {
        this.$message({
          type:'error',
          message:"信息过期，请重新登录"
        })
        this.$router.push('/Login')
      }
  },
  created() {
    //var token = localStorage.getItem("token");
    var teacherName = localStorage.getItem("username");
    console.log("name === " + teacherName); 
    console.log("token == " + token);
    if (this.token) {
      this.$http({
        methods: "get",
        url: "/getInfo?teacherName=" + teacherName,
        headers: {
          token: token
        }
      })
        .then(response => {
          if (response.data.code == "200") {
            console.log("success");
            this.updateForm = response.data.teacher;
            this.updateForm.password = "";
          } else {
            this.$router.push("/Login");
          }
        })
        .catch(error => {
          this.$message({
            type: "error",
            message: "请登录后再试！"
          });
          this.$router.push("/Login");
        });
    } else {
      this.$router.push("/Login");
    }
  }
};
</script>

<style>

</style>


