<template>
  <div class="bg-login">
    <div class="forget-header container">
	</div>
      <div class="middle-boxlforget forgetscreen bg-white container">
            <div class="forget-title text-center">
                <h2 style="text-align:center;margin-top:5px;">找回密码</h2>
            </div>
            <el-form inline-message :model="forgetForm" :rules="forgetRules" ref="forgetForm" class="forget-Form">
                <el-form-item prop="name">
                    <el-input status-icon prefix-icon="fa fa-user" v-model="forgetForm.name" placeholder="请输入用户名" ></el-input>
                </el-form-item>
                <el-form-item prop="checkCode">
                    <el-input prefix-icon="fa fa-lock" @keyup.enter.native="nextStep"
                        v-model="forgetForm.checkCode" placeholder="请输入验证码" auto-complete="on">
                    </el-input>
                    <div class="divIdentifyingCode" @click="getIdentifyingCode(true)">
                        <img id="imgIdentifyingCode" style="height:40px; width: 100px; cursor: pointer;" alt="点击更换"
                            title="点击更换" />
                    </div>
                </el-form-item>
                <el-form-item label="">
                    <el-button type="primary" :loading="loading" @click.native.prevent="nextStep" style="width:400px;">下一步</el-button>
                </el-form-item>
            </el-form>
            <div class="return_login">
                <div class="login"><router-link to="/Login"> <a href="#">返回登录</a></router-link></div>
            </div>
      </div>
  </div>
</template>

<script>
//import store from '../store/index.js'
export default {
    name: 'ForgetPass',
    inject:['reload'],
    data() {
        return {
            forgetForm:{
                name:'',
                checkCode:''
            },
            url:'',
            forgetRules:{
                name:[{required:true, message:'用户名不能为空',trigger:'blur'},{min:5, max:20, message:'长度在 5 到 20 个字符', trigger: 'blur'}],
                checkCode:[{required:true,message:'验证不能为空',trigger:'blur'},{min:4,message:'验证码长度必须为4位',trigger:'blur'}]
            },
            loading:false,
            token: ''
        }
    },
    created() {
        
    },
    mounted() {
        // this.url = "http://localhost:8088/api/createImageCode?" + Math.random();
        // let objs = document.getElementById("imgIdentifyingCode");
        // objs.src = this.url;
        this.getIdentifyingCode(true);
    },
    watch: {
        token: {
            handler:function(token) {
                localStorage.save(token)
            },
            deep:true
        }
    },
    methods: {
        nextStep: function() {
            this.$refs.forgetForm.validate(valid => {
                if (valid){
                    if (this.forgetForm.name === null || this.forgetForm.name === '') {
                        this.$message({
                            type:'error',
                            message: '用户名不能为空'
                        })
                        return;
                    }

                    if (this.forgetForm.checkCode === null || this.forgetForm.checkCode === '') {
                        this.$message({
                            type: 'error',
                            message: '验证码不能为空'
                        })
                        return;
                    }
                    //this.loading = true
                    //alert(this.forgetForm.name)
                    this.$http.get('/validate?name='+this.forgetForm.name + '&checkCode='+this.forgetForm.checkCode).then((res) => {
                        console.log(res.data)
                        if(res.data.code === 200) {
                                console.log("success")
                                this.$message({
                                    type: 'success',
                                    message: res.data.message,
                                })
                                this.$router.push('/Appeal')
                            }else if(res.data.code == 1023){
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }else if (res.data.code == 400) {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.message
                                })
                                this.getIdentifyingCode(true);
                            }
                    }).catch((err)=>{
                            this.$message({
                                type: 'error',
                                message: '网络错误，请重试' 
                            })
                            this.getIdentifyingCode(true);
                    })
                    
                }
            })
        },
        /**
         * 窗口代码
         * @param {Boolean} bRefresh 是否刷新
         */
        getIdentifyingCode: function (bRefresh) {
            let identifyCodeSrc =  "http://localhost:8088/api/createImageCode";
            if (bRefresh) {
                identifyCodeSrc = "http://localhost:8088/api/createImageCode?" + Math.random();
            }
            let objs = document.getElementById("imgIdentifyingCode");
            objs.src = identifyCodeSrc;
            }
        }
}
</script>

<style>
body{
    background: url("~@/assets/bk1.jpg") no-repeat center fixed;
    background-size: cover;
}
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
.el-form-item__error--inline {
    display: flex;
}

.bg-white{
    background-color: #ffffff;
    opacity: 0.8;
}
.middle-boxlforget{
    max-width: 400px;
    z-index: 100;
    margin: 0 auto;
    padding-bottom: 30px;
}

.forgetscreen .middle-box{
    width: 300px;
}
.forget-title{
	height:85px;
	padding:30px;
	font-weight:bold;
}
.forget-header{
	height:150px;
}
.forget-logo{
	margin-top:40px;
}
.forget-logo-zh,
.forget-logo-en{
	font-weight:bold;
	color:#3b3127;
}
.forget-logo-zh{
	font-size:45px;
	font-family:"宋体";
}
.forget-logo-en{
	font-size:21px;
	font-family:"Helvetica";
}
.forget-Form {
    margin-top: 0px;
    padding-top: 0;
    margin-top: -30px;
}
.middle-box {
    height: 370px;
}
.divIdentifyingCode {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    width: 102px; /*设置图片显示的宽*/
    height: 40px; /*图片显示的高*/
    background: #e2e2e2;
    margin: 0;
  }
a {
    text-decoration: none;
}
a:link{color:blue;}
a:visited{color:#409EFF;}
a:hover{color: red;font-size: 16px;}
a:active{color: lightskyblue;}
</style>


