<template>
  <div style="background:white;">
    <div class="signdetail">
      <el-table :data="SignDetails" height="600" border style="width: 100%">
        <el-table-column prop="studentName" label="姓名" width="120"></el-table-column>
        <el-table-column prop="number" label="学号" width="180"></el-table-column>
        <el-table-column prop="course" label="课程" width="150"></el-table-column>
        <el-table-column prop="checkType" label="签到类型" width="130"></el-table-column>
        <el-table-column prop="checkTime" label="签到时间" width="180"></el-table-column>
        <el-table-column prop="signoutTime" label="签退时间" width="180"></el-table-column>
        <el-table-column prop="sign_state" label="考勤状态" width="120"></el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="primary" size="mini">更新</el-button>
            <el-button @click="deleteClick(scope.row)" type="danger" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="opearte">
      <el-button type="primary" icon="el-icon-back" @click="back">返回</el-button>
      <el-button type="primary" @click="download">
        下载
        <i class="el-icon-download el-icon--right"></i>
      </el-button>
    </div>
    <div class="pagination" style="margin-left:40%;margin-top:10px;">
      <el-pagination background layout="prev, pager, next" :current-page.sync="pagination.currentPage" 
      :page-size.sync="pagination.currentSize" :total="pagination.count"
      @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script>
var FileSaver = require('file-saver')
import { saveAs } from 'file-saver';

export default {
  name: "SignDetail",
  created() {
    this.getSignDetails();
    //this.findByPage(this.pagination.currentPage,this.pagination.currentSize);
  },
  watch: {
    //监测路由变化，只要变化了就调取路由参数方法将数据存储在本组件
    $route: "getSignDetails"
  },
  data() {
    return {
      SignDetails: [],
      //初始分页条件
      pagination:{
        currentPage: 1,//当前页
        currentSize: 2, //每页显示的记录数
        count: 0 //记录总数
      },
      ExcelData:[],
      excelName:''
    };
  },
  methods: {
    deleteClick(row) {
        console.log("checkTime = " + row.checkTime);
        var SignDetail = {};
        SignDetail.number = row.number;
        SignDetail.checkType = row.checkType;
        SignDetail.checkTime = row.checkTime;
        var token = localStorage.getItem("token");

        this.$confirm("确定删除吗?").then(_=>{
          if (token) {
            this.$http.post("/deleteSignDetail",SignDetail).then((res)=>{
                if (res.data.code == 200) {
                  this.$notify({
                    type:"success",
                    message:res.data.message
                  })
                  //location.load();
                  this.$router.go(0)
                }else {
                  this.$notify({
                    type:"success",
                    message:res.data.message
                  })
                }
            }).catch((err)=>{
              this.$message({
                type:"error",
                message:err.data.message
              })
            })
        }else {
          this.$message({
            type:'error',
            message:"身份失效，请重新登录！"
          })
          this.$router.push("/Login")
        }
        }).catch(_=>{
          
        })
    },
    handleClick(row) {
      var token = localStorage.getItem("token");
      this.$http.get("/find_sign_detail?className="+row.className+"&signDate="+row.signDate).then((res)=>{
        if (res.data.code == 200) {
           this.$router.push({path:'/SignDetail',query:{SignDetails:res.data.data,excelName:row.className}})
        }else {
          this.$message({
            type:'error',
            message:res.data.message
          })
        }
      }).catch((err)=>{
        this.$message({
          type:'error',
          message:err.data.message
        })
      })
      console.log(row.className);
    },
    getSignDetails() {
      //取由路由带过来的参数
      var data = this.$route.query.SignDetails;
      this.excelName = this.$route.query.excelName;
      //将数据放在当前的组件内
      this.SignDetails = data;
    },
    //返回上一级
    back() {
        this.$router.go(-1);
    },
    handleDate:function(value) {
    return value <10 ?'0'+value:value;
    },
    findByPage(currentPage,pageSize) {
       //const params = this.pagination
       //var token = localStorage.getItem("token");
       var token = localStorage.getItem("token");
      if (token) {
        this.$http.get("/findByPageDetail?currentPage="+currentPage+"&pageSize="+pageSize).then((res)=>{
            //alert(res.data.count)
            this.pagination.count = res.data.count;
            this.SignDetails = res.data.row;
        }).catch((err)=>{
          console.log("test============")
          this.$message({
            type:"error",
            message:"网络异常"
          })
        })
      }
    },
    handleCurrentChange(page) {
      this.findByPage(page,this.pagination.currentSize);
    },
    download1(){
        const defaultCellStyle = {font: { name: "Verdana", sz: 11, color: "FF00FF88"}, fill: {fgColor: {rgb: "FFFFAA00"}}};
        const wopts = { bookType:'xlsx', bookSST:false, type:'binary', defaultCellStyle: defaultCellStyle, showGridLines: false};
        const wb = { SheetNames: ['Sheet1'], Sheets: {}, Props: {} }; 
        let data = this.SignDetails;
        console.log(data)
        const titles = ["姓名","学号","课程","签到类型","签到时间","签退时间","考勤状态"]
        const headers = ["studentName","number","course","checkType","checkTime","sign_out","sign_state"];
        //const ws = XLSX.utils.json_to_sheet(data,{header:titles})
        //let data = this.ExcelData;["name","number","course","checkType","last_check_time","sign_out","sign_state"]
        //wb.Sheets['Sheet1'] = XLSX.utils.json_to_sheet(data,{header:titles})
        // const range = XLSX.utils.decode_range(wb['!ref'])
        // for(let c = range.s.c; c <= range.e.c; c++) {
        //   const header = XLSX.utils.encode_col(c) + '1'
        //   wb[header].v = titles[ wb[header].v ]
        // }
        wb.Sheets['Sheet1'] = XLSX.utils.json_to_sheet(data)
        //创建二进制对象写入转换好的字节流
        let tmpDown =  new Blob([this.s2ab(XLSX.write(wb, wopts))],{ type: "application/octet-stream" })
        FileSaver.saveAs(tmpDown, this.excelName +".xlsx");
    },
    //字符串转字符流
    s2ab (s) {
    if (typeof ArrayBuffer !== 'undefined') {  
        var buf = new ArrayBuffer(s.length);  
        var view = new Uint8Array(buf);  
        for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;  
        return buf;  
    } else {  
        var buf = new Array(s.length);  
        for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;  
        return buf;  
      }
    },
    download() {
      require.ensure([], () => {
              const { export_json_to_excel } = require('@/excel/Export2Excel.js')
              const tHeader = ["姓名","学号","课程","签到类型","签到时间","签退时间","考勤状态"]  //tHeader为导出Excel表头名称，
              const filterVal= ["studentName","number","course","checkType","checkTime","signoutTime","sign_state"]
              const list = this.SignDetails;// this.SignDetails为服务端返回的签到详情
              const data = this.formatJson(filterVal, list)  //filterVal为传进来的数组res的键名
              export_json_to_excel(tHeader, data, this.excelName) //'签到详情表'即为导出Excel名称。
          })
    },
    formatJson(filterVal, jsonData) {
          return jsonData.map(v => filterVal.map(j => v[j]))
      },
    // 创建一个worksheet,创建过程中设置表头
    // createWs(data, fields, titles) {
    //     const ws = XLSX.utils.json_to_sheet(
    //         data, 
    //         { 
    //             header: fields
    //         }
    //     )
    //     const range = XLSX.utils.decode_range(ws['!ref'])
        
    //     for(let c = range.s.c; c <= range.e.c; c++) {
    //       const header = XLSX.utils.encode_col(c) + '1'
    //       ws[header].v = titles[ ws[header].v ]
    //     }
        
    //     return ws
    // },
    formatDate:function(value) {
          var date = new Date(value);
          var year = date.getFullYear();
          var month = this.handleDate(date.getMonth()+1);
          var day = this.handleDate(date.getDate());
          var hours = this.handleDate(date.getHours());
          var minutes = this.handleDate(date.getMinutes());
          var seconds = this.handleDate(date.getSeconds());
          return year + '-' + month + '-' + day + ' ' + hours + ":" + minutes + ":" + seconds;
    }
  }
};
</script>
<style>
.opearte {
    margin-top: 30px;
    margin-right: 20px;
    float: right;
}
</style>


