<template>
  <div class="details" style="background:white;">
    <div class="operation">
      <el-form
        :inline="true"
        :model="signDetail"
        :rules="qurryFormRules"
        ref="signDetail"
        class="demo-form-inline"
      >
        <el-form-item label="班级:" required>
          <el-select v-model="signDetail.className" placeholder="请选择班级:">
            <el-option label="初一(1)班" value="初一(1)班"></el-option>
            <el-option label="初一(2)班" value="初一(2)班"></el-option>
            <el-option label="初一(3)班" value="初一(3)班"></el-option>
            <el-option label="初一(4)班" value="初一(4)班"></el-option>
            <el-option label="初一(5)班" value="初一(5)班"></el-option>
            <el-option label="初二(1)班" value="初二(1)班"></el-option>
            <el-option label="初二(2)班" value="初二(2)班"></el-option>
            <el-option label="初二(3)班" value="初二(3)班"></el-option>
            <el-option label="初二(4)班" value="初二(4)班"></el-option>
            <el-option label="初二(5)班" value="初二(5)班"></el-option>
            <el-option label="初三(1)班" value="初三(1)班"></el-option>
            <el-option label="初三(2)班" value="初三(2)班"></el-option>
            <el-option label="初三(3)班" value="初三(3)班"></el-option>
            <el-option label="初三(4)班" value="初三(4)班"></el-option>
            <el-option label="初三(5)班" value="初三(5)班"></el-option>
            <el-option label="高一(1)班" value="高一(1)班"></el-option>
            <el-option label="高一(2)班" value="高一(2)班"></el-option>
            <el-option label="高一(3)班" value="高一(3)班"></el-option>
            <el-option label="高一(4)班" value="高一(4)班"></el-option>
            <el-option label="高一(5)班" value="高一(5)班"></el-option>
            <el-option label="高二(1)班" value="高二(1)班"></el-option>
            <el-option label="高二(2)班" value="高二(2)班"></el-option>
            <el-option label="高二(3)班" value="高二(3)班"></el-option>
            <el-option label="高二(4)班" value="高二(4)班"></el-option>
            <el-option label="高二(5)班" value="高二(5)班"></el-option>
            <el-option label="高三(1)班" value="高三(1)班"></el-option>
            <el-option label="高三(2)班" value="高三(2)班"></el-option>
            <el-option label="高三(3)班" value="高三(3)班"></el-option>
            <el-option label="高三(4)班" value="高三(4)班"></el-option>
            <el-option label="高三(5)班" value="高三(5)班"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学生姓名:" required>
          <el-input v-model="signDetail.studentName" placeholder="请输入学生姓名"></el-input>
        </el-form-item>
        <el-form-item label="时间" required>
          <el-col :span="10">
            <el-form-item prop="date1">
              <el-date-picker
                type="date"
                placeholder="选择起始日期"
                v-model="signDetail.checkTime"
                @change="handleDate"
                format="yyyy-MM-dd"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="1">&nbsp;&nbsp;-</el-col>
          <el-col :span="10">
            <el-form-item prop="date2">
              <el-date-picker
                type="date"
                placeholder="选择结束日期"
                v-model="signDetail.signoutTime"
                format="yyyy-MM-dd"
                @change="handleDate"
              style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table clearfix" style="margin-top:-15px;">
      <el-table
        :data="signRecords"
        :highlight-current-row="true"
        height="500"
        border
        style="width: 100%;"
      >
        <el-table-column prop="signDate" label="日期" width="180"></el-table-column>
        <el-table-column prop="className" label="班级" width="180"></el-table-column>
        <el-table-column prop="courses" label="课程" width="180"></el-table-column>
        <el-table-column prop="totalNumber" label="班级人数" width="180"></el-table-column>
        <el-table-column prop="actualNumber" label="出勤人数" width="180"></el-table-column>
        <el-table-column prop="tacherName" label="班主任" width="180"></el-table-column>
        <el-table-column label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="primary" size="mini">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination" style="margin-left:40%;">
      <el-pagination background layout="prev, pager, next" :current-page.sync="pagination.currentPage" 
      :page-size.sync="pagination.currentSize" :total="pagination.count"
      @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: "Courses",
  data() {
    return {
      signDetail: {
        className: "",
        studentName: "",
        checkTime: "",
        signoutTime: "",
        number:'',
        checkType:''
      },
      //初始分页条件
      pagination:{
        currentPage: 1,//当前页
        currentSize: 7, //每页显示的记录数
        count: 0 //记录总数
      },
      SignDetails:[],
      excelName:"",
      signRecords:[],
      qurryFormRules: {}
    }
  },
  created() {
    var token = localStorage.getItem("token");
    if (true) {
      this.$http.get("/getSignRecord",{headers:{'token':token}}).then((res)=>{
          if(res.data.code == 200) {
             console.log("data = " + res.data.data)
             this.signRecords = res.data.data;
          }
      }).catch((err)=>{
        this.$message({
          type:"error",
          message:"网络异常"
        })
      })
    }
    this.findByPage(this.pagination.currentPage,this.pagination.currentSize);
  },
  methods: {
      handleClick(row) {
      var token = localStorage.getItem("token");
      this.$http.get("/find_sign_detail?className="+row.className+"&teacherName="+row.tacherName).then((res)=>{
        if (res.data.code == 200) {
           this.$router.push({path:'/SignDetail',query:{SignDetails:res.data.data,excelName:row.className}})
        }else {
          this.$message({
            type:'error',
            message:res.data.message
          })
        }
      }).catch((err)=>{
        this.$message({
          type:'error',
          message:err.data.message
        })
      })
      console.log(row.className);
    },
    handleDate(value) {
      //const temp = new Date(date);
      if (value != "" || value != undefined) {
        var date = new Date(value);
        var year = date.getFullYear();
        var month =
          date.getMonth() + 1 >= 10
            ? date.getMonth() + 1
            : "0" + (date.getMonth() + 1);
        var day = date.getDay() >= 10 ? date.getDay() : "0" + date.getDay();
        var newDate = year + "-" + month + "-" + day;
        return newDate;
      }
    },
      findByPage(currentPage,pageSize) {
       //const params = this.pagination
       var token = localStorage.getItem("token");
       var token = localStorage.getItem("token");
      if (true) {
        this.$http.get("/findByPage?currentPage="+currentPage+"&pageSize="+pageSize).then((res)=>{
            //alert(res.data.count)
            this.pagination.count = res.data.count;
            this.signRecords = res.data.row;
        }).catch((err)=>{
          console.log("test============")
          this.$message({
            type:"error",
            message:"网络异常"
          })
        })
      }
    },
    handleCurrentChange(page) {
      //this.pagination.page = page;
      //this.getStoreFileList();
      this.findByPage(page,this.pagination.currentSize);
    },
    onSubmit: function() {
      this.signDetail.checkTime = this.handleDate(this.signDetail.checkTime);
      this.signDetail.signoutTime = this.handleDate(this.signDetail.signoutTime);
      console.log(this.signDetail.className + "-" + this.signDetail.studentName + "-" + this.signDetail.checkTime + "-----" + this.signDetail.signoutTime);
      var token = localStorage.getItem("token");
      //alert(token)
      if (token) {
        this.$http.post("/find_detail_by_condition",this.signDetail).then((res)=>{
        if (res.data.code == 200) {
          //  if (this.signDetail.className != '' && this.signDetail.studentName != '') {
          //    this.excelName = this.SignDetail.className;
          //  }else if (this.signDetail.className != '' && this.signDetail.studentName == '') {
          //    this.excelName = this.SignDetail.className;
          //  }else if (this.signDetail.className == '' && this.signDetail.studentName != '') {
          //    this.excelName = this.signDetail.studentName;
          //  }else {
          //    this.excelName = '签到列表';
          //  }
          console.log("test = " + this.signDetail.studentName)
           this.$router.push({path:'/SignDetail',query:{SignDetails:res.data.data,excelName:this.excelName}})
        }else {
          this.$message({
            type:'error',
            message:res.data.message
          })
        }
      }).catch((err)=>{
        this.$message({
          type:'error',
          message:err.data.message
        })
      })
      }
    }
  }
};
</script>
<style scoped>
.operation {
  padding-bottom: 5px;
  padding-top: 5px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
  padding-bottom: 20px;
}

.tableWrap {
  margin-top: 50px;
  border: 1px solid #333;
  height: 500px;
  width: 100%;
}

.tableWrap .item {
  width: 100px;
  border-right: 1px solid #333;
  float: left;
  height: 100%;
}
.tableWrap .item .name {
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-bottom: 1px solid #333;
}
.tableWrap .item .itemdetail {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.el-form--inline .el-form-item {
  margin-right: 0px;
}
.el-row {
    background-color: white;
}
.el-input__inner {
    padding-left: 30px;
}
</style>


